@import "../../global.scss";

.portfolio {
  background-color: $bannerColor;
  display: flex;
  flex-direction: column;
  align-items: center;
  

  h1 {
    font-size: 50px;
    color: $secondaryColor;

    @include mobile{
      display: none;
    }
  }

  ul {
    margin: 10px;
    padding: 0;
    list-style: none;
    display: flex;

    @include mobile{
      margin: 10px 0;
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  .container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;

    @include mobile{
      width: 100%;
    }

    .item {
      width: 13.75rem;
      height: 9.375rem;
      border-radius: 0.5rem;
      border: 1px solid rgb(240, 239, 239);
      margin: 1rem;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      position: relative;
      transition: all .5s ease;
      cursor: pointer;

      @include mobile{
        width: 130px;
        height: 100px;
      }

      h3 {
        position: absolute;
        font-size: 1rem;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
      }

      &:hover {
        background-color: $mainColor;
        img {
          opacity: 0.2;
          z-index: 0;
        }
      }
    }
  }
}
