.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9;

  /* background-color: rgba(0, 0, 0, 0.6); */
  animation-duration: 0.25s;
  animation-timing-function: ease-out;
  animation-name: fadeIn;
  animation-fill-mode: forwards;
  display: flex;
  justify-content: center;
  align-items: center;

  &.close {
    animation-name: fadeOut;
  }

  button {
    outline: none;
    cursor: pointer;
    border: 0;
  }

  > section {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    border-radius: 0.3rem;

    /* background-color: #fff; */
    overflow: hidden;
    animation-duration: 0.25s;
    animation-timing-function: ease-out;
    animation-name: slideUp;
    animation-fill-mode: forwards;
  }

  &.close > section {
    animation-name: slideDown;
  }

  > section > {
    button {
      position: absolute;
      top: 15px;
      right: 15px;
      width: 30px;
      font-size: 21px;
      font-weight: 700;
      text-align: right;
      color: #999;
      background-color: transparent;
    }

    footer {
      padding: 12px 16px;
      text-align: right;

      button {
        padding: 6px 12px;
        color: #fff;
        background-color: #6c757d;
        border-radius: 5px;
        font-size: 13px;
      }
    }
  }
}

@keyframes modal-bg-show {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes slideUp {
  from {
    transform: translateY(-100px);
  }

  to {
    transform: translateY(0px);
  }
}

@keyframes slideDown {
  from {
    transform: translateY(0px);
  }

  to {
    transform: translateY(-100px);
  }
}